import {
  gamePathNames,
  getSoundEffectNames,
  ISoundSystemControlContext,
  notifyBarManager,
  liveStatsManager,
} from "components";
import { fetchBase, rangeBetween, sleep, track } from "utils";
import { SeedContextType } from "providers";
import { Coin, coinsManager } from "hooks";

import { calcChance, calcProfit, getFromLocal, getLimits } from "./utils";
import { LimboPanel, PlayFetch } from "./types";
import { LimboEmitter } from "./emitter";
import { limboConfig } from "../config";

export * from "./types";
export * from "./utils";

export class LimboManager {
  panel: LimboPanel = {
    manual: {
      betAmount: getFromLocal().betAmount || 0.1,
      profit: 0.1,
      chance: 48.5,
      payout: getFromLocal().payout || 0.1,
    },
    auto: {
      sys: {
        isOpen: false,
        isStarted: false,

        baseBet: 0.1,
      },
      operations: {
        onLoss: {
          betPercent: 100,
          cashout: 1.9,
          isCashout: false,
          variant: 1,
        },
        onWin: {
          betPercent: 0,
          cashout: 1.9,
          isCashout: false,
          variant: 1,
        },
      },
      limits: {
        numberOfBets: 0,
        stopOnLoss: 0,
        stopOnWin: 0,
      },
      usage: {
        numberOfBets: 0,
        profit: 0,
      },
    },

    limits: {},
  };
  emitter: LimboEmitter;
  seed: SeedContextType;
  sound: ISoundSystemControlContext;

  constructor(seed: SeedContextType, sound: ISoundSystemControlContext) {
    this.seed = seed;
    this.sound = sound;

    getLimits((limits) => {
      this.panel.limits = limits;
      let { coin } = coinsManager.selectedCoin || {};

      if (!coin) {
        try {
          const localCoin: Coin | null = JSON.parse(
            localStorage.getItem("selectedCoin") || "",
          );

          coin = localCoin ? localCoin.coin : "GEM";
        } catch (error) {
          coin = "GEM";
        }
      }

      if (coin) {
        const { maximum_bet, minimum_bet } = limits[coin];
        const { betAmount, payout } = getFromLocal();
        this.emitter.setManualValues(
          "betAmount",
          rangeBetween(betAmount, Number(minimum_bet), Number(maximum_bet)),
        );

        const newPayout = rangeBetween(
          payout,
          Number(limboConfig.payout.min),
          Number(limboConfig.payout.max),
        );
        this.emitter.setManualValues("payout", newPayout);
        this.emitter.setManualValues("chance", calcChance(newPayout));
      }
    });

    this.emitter = new LimboEmitter(this);
  }

  onClick() {
    const { auto, manual } = this.panel;
    const { sys, usage } = auto;

    if (sys.isOpen) {
      sys.baseBet = manual.betAmount;

      this.emitter.setAuto({
        isStarted: !sys.isStarted,
      });

      for (const key in usage) {
        //@ts-ignore because key is string
        if (usage[key]) usage[key] = 0;
      }

      this.playAuto();
    } else {
      this.play();
    }
  }

  async play() {
    const { clientSeed, rotateClientSeed, updateNonce } = this.seed;
    const { controlFunctions } = this.sound;
    const { betAmount, payout } = this.panel.manual;
    const { coin } = coinsManager.selectedCoin || {};
    const fetchParams = {
      client_seed: clientSeed?.seed,
      bet_amt: betAmount.toFixed(8),
      coin,
      target_payout: payout.toFixed(2),
    };

    const { data, error } = await fetchBase<PlayFetch>("limbo/play", {
      variables: fetchParams,
    });
    if (data?.success) {
      const profit = Number(data.profit);

      if (data?.profit !== undefined) {
        const { lose, win } = getSoundEffectNames(gamePathNames.LIMBO);

        const { playEffect } = controlFunctions;
        const t = setTimeout(() => {
          profit >= 0 ? playEffect(win) : playEffect(lose);
          clearTimeout(t);
        }, 1000);
      }

      this.rocketLaunch(data.actual_payout, profit >= 0);

      liveStatsManager.addBet({
        amount: betAmount,
        coin: data.coin,
        profit,
      });

      coinsManager.coinValueUpdate(data.balance, data.coin);
    }

    if (error) {
      notifyBarManager.error({
        message: error,
        timeOut: 4000,
      });
    }

    if (!clientSeed?.isSetManually) {
      rotateClientSeed();
    } else {
      updateNonce();
    }

    return data;
  }

  async playAuto() {
    if (!this.panel.auto.sys.isStarted) {
      return;
    }

    const stopAuto = () => {
      this.emitter.emitAutoLimits(this.panel.auto.limits);

      this.emitter.setAuto({
        isStarted: false,
      });
    };

    try {
      const result = await this.play();
      if (result?.success) {
        const { win, coin, profit, balance } = result;
        const { limits, manual, auto } = this.panel;

        const { maximum_bet, minimum_bet } = limits[coin];
        const { betAmount, payout } = manual;
        const { variant, isCashout, cashout, betPercent } =
          auto.operations[win ? "onWin" : "onLoss"];

        const { limits: autoLimits, usage, sys } = auto;

        usage.profit += Number(profit);
        usage.numberOfBets += 1;

        switch (variant) {
          case 0: //Change Bet By (%)
            let newBetAmount = betAmount + betAmount * (betPercent / 100);
            newBetAmount = rangeBetween(
              Number(newBetAmount.toFixed(8)),
              minimum_bet,
              Math.min(maximum_bet),
            );

            this.emitter.setManualValues("betAmount", newBetAmount);
            this.emitter.setManualValues(
              "profit",
              calcProfit(newBetAmount, payout),
            );

            if (newBetAmount > Number(balance)) {
              stopAuto();
              notifyBarManager.info({
                message: "Auto mode is stopped due to insufficient balance",
                timeOut: 8000,
              });
              return;
            }
            break;
          case 2: //Stop Betting
            stopAuto();
            return;
          case 1: //Reset to Base Bet
          default:
            this.emitter.setManualValues("betAmount", sys.baseBet);
            break;
        }

        if (isCashout) {
          const newPayout = Number(cashout.toFixed(2));
          this.emitter.setManualValues("payout", newPayout);
        }

        const { numberOfBets, stopOnLoss, stopOnWin } = autoLimits;
        const isStopBecauseLoss = stopOnLoss && usage.profit <= stopOnLoss * -1,
          isStopBecauseWin = stopOnWin && usage.profit >= stopOnWin,
          isStopBecauseTooMuchBets =
            numberOfBets && numberOfBets - usage.numberOfBets <= 0;

        if (isStopBecauseLoss || isStopBecauseWin || isStopBecauseTooMuchBets) {
          stopAuto();
          return;
        }

        if (sys.isStarted) {
          if (numberOfBets) {
            this.emitter.emitAutoLimits({
              ...autoLimits,
              numberOfBets: numberOfBets - usage.numberOfBets,
            });
          }
        }

        await sleep(1 / 4); //no more than 1/4 second
        this.playAuto();
      } else {
        stopAuto();

        if (result?.message) {
          notifyBarManager.error({
            message: result.message,
            timeOut: Infinity,
          });
        }
      }
    } catch (error) {
      stopAuto();
      track()?.errors(error);

      console.error(" >>> limbo auto play error. ", error);
      notifyBarManager.error({
        message: "Something went wrong",
        timeOut: Infinity,
      });
    }
  }

  rocketLaunch(rocketPayout: string | number, isWin: boolean) {
    this.emitter.setRocket(rocketPayout, isWin);
  }
}
