import React, { FC } from "react";
import clsx from "clsx";
import { ReactComponent as AllInclusiveIcon } from "assets/icons/all-inclusive.svg";

import { GetCoinImg, Input, InputProps, InputAdornment } from "components";

import css from "./style.module.scss";
import { useCoins } from "hooks";

interface Props extends InputProps {
  withImg?: boolean;
  coin?: string;

  infinity?: boolean;
}

export const LimboInput = React.forwardRef<HTMLInputElement, Props>(
  ({ className, infinity, withImg, coin, ...props }, ref) => {
    const { showInUSD, selectedCoin } = useCoins();
    let startAdornment: JSX.Element | undefined;
    if (withImg) {
      startAdornment = (
        <InputAdornment position="start">
          <GetCoinImg className={css.coinImg} coin={coin} />
          {showInUSD && selectedCoin?.coin !== "GEM" ? (
            <span className={css.usdAdornment}>$</span>
          ) : null}
        </InputAdornment>
      );
    }

    return (
      <div className={clsx(css.wrapper, className)}>
        <Input
          {...props}
          classes={{
            root: css.root,
            disabled: css.disabled,
          }}
          ref={ref}
          startAdornment={startAdornment}
          endAdornment={
            props.endAdornment ? (
              props.endAdornment
            ) : infinity ? (
              <InputAdornment position="end">
                <AllInclusiveIcon className={css.infinityImg} />
              </InputAdornment>
            ) : null
          }
        />
      </div>
    );
  },
);
