import React, { useEffect, useState } from "react";

import { useLimboControl } from "pages/Limbo";
import { Rocket } from "./components/Rocket";
import Roller from "./components/Roller";
import css from "./style.module.scss";

const Limbo = () => {
  const [isStarted, setIsStarted] = useState(false);
  const { manager } = useLimboControl();

  useEffect(() => {
    const unsubscribe = manager.emitter.rocketListener(({ isAuto }) => {
      if (!isAuto) setIsStarted(true);
    });

    return () => unsubscribe();
  }, []);

  return (
    <div className={css.root}>
      <div className={css.footer} />
      <Roller />
      <Rocket isStarted={isStarted} setNoStarted={() => setIsStarted(false)} />
    </div>
  );
};

export default React.memo(Limbo);
