import { useEffect, useState } from "react";
import { useLimboControl } from "../index";

export const useIsAutoOpen = () => {
  const { manager } = useLimboControl();
  const [isAuto, setIsAuto] = useState(false);

  const setAuto = (value: boolean) => {
    manager.emitter.setAuto({
      isOpen: value,
      isStarted: false,
    });
  };

  useEffect(() => {
    const removeListener = manager.emitter.autoListener(({ isOpen }) => {
      setIsAuto(isOpen);
    });
    return () => {
      removeListener();
    };
  }, []);

  return {
    isAuto,
    setAuto,
  };
};
