import { EventEmitter } from "events";
import { LimboManager } from ".";
import {
  CHANGE_AUTO,
  CHANGE_AUTO_LIMITS,
  CHANGE_AUTO_OPERATIONS,
  CHANGE_MANUAL_BET_VALUE,
  CHANGE_ROCKET,
} from "./events";
import { AutoPanel, ManualPanel } from "./types";
import { calcChance, calcPayout, calcProfit, saveToLocal } from "./utils";

export class LimboEmitter extends EventEmitter {
  manager: LimboManager;
  constructor(manager: LimboManager) {
    super();

    this.manager = manager;
  }

  setManualValues(key: keyof ManualPanel, value: number | string) {
    const { manual } = this.manager.panel;
    manual[key] = Number(value);

    switch (key) {
      case "betAmount":
        manual.profit = calcProfit(manual.betAmount, manual.payout);
        break;

      case "payout":
        manual.chance = calcChance(manual.payout);
        break;
      case "chance":
        manual.payout = calcPayout(manual.chance);
        manual.chance = calcChance(manual.payout);
        manual.payout = calcPayout(manual.chance);
        break;

      default:
        break;
    }

    saveToLocal({
      betAmount: manual.betAmount,
      payout: manual.payout,
    });

    this.emit(CHANGE_MANUAL_BET_VALUE, manual);
  }
  manualValuesListener(callBack: (values: ManualPanel) => void) {
    this.addListener(CHANGE_MANUAL_BET_VALUE, callBack);

    return () => {
      this.removeListener(CHANGE_MANUAL_BET_VALUE, callBack);
    };
  }

  setRocket(value: string | number, isWin: boolean) {
    const isAuto = this.manager.panel.auto.sys.isOpen;
    this.emit(CHANGE_ROCKET, {
      value,
      isWin,
      isAuto,
    });
  }
  rocketListener(
    callBack: (value: {
      value: string | number;
      isWin: boolean;
      isAuto: false;
    }) => void,
  ) {
    this.addListener(CHANGE_ROCKET, callBack);

    return () => {
      this.removeListener(CHANGE_ROCKET, callBack);
    };
  }

  setAuto(values: { isOpen?: boolean; isStarted?: boolean }) {
    const { auto } = this.manager.panel;
    this.emitAutoLimits(auto.limits);

    auto.sys = {
      ...auto.sys,
      ...values,
    };

    this.emit(CHANGE_AUTO, auto.sys);
  }
  autoListener(
    callBack: (values: { isOpen: boolean; isStarted: boolean }) => void,
  ) {
    this.addListener(CHANGE_AUTO, callBack);

    return () => {
      this.removeListener(CHANGE_AUTO, callBack);
    };
  }

  setAutoValues(values: AutoPanel["operations"]) {
    this.manager.panel.auto.operations = values;
    this.emit(CHANGE_AUTO_OPERATIONS, values);
  }
  autoValuesListener(callBack: (values: AutoPanel["operations"]) => void) {
    this.addListener(CHANGE_AUTO_OPERATIONS, callBack);

    return () => {
      this.removeListener(CHANGE_AUTO_OPERATIONS, callBack);
    };
  }

  emitAutoLimits(value: AutoPanel["limits"]) {
    this.emit(CHANGE_AUTO_LIMITS, value);
  }
  setAutoLimits(value: AutoPanel["limits"]) {
    this.manager.panel.auto.limits = value;
    this.emitAutoLimits(value);
  }
  autoLimitsListener(callBack: (value: AutoPanel["limits"]) => void) {
    this.addListener(CHANGE_AUTO_LIMITS, callBack);

    return () => {
      this.removeListener(CHANGE_AUTO_LIMITS, callBack);
    };
  }
}
