import React, { useEffect, useState } from "react";
import clsx from "clsx";

import { AnimateNumbers } from "components";
import { useLimboControl } from "pages/Limbo";

import { rocketAnimationTime } from "../Rocket";
import css from "./style.module.scss";

interface State {
  value: string | number;
  isWin: boolean;
  isAuto: false;
}

const Roller = () => {
  const { manager } = useLimboControl();
  const [rollerState, setRollerState] = useState<State>({
    value: "1.00",
    isWin: true,
    isAuto: false,
  });

  useEffect(() => {
    const unsubscribe = manager.emitter.rocketListener(
      ({ isAuto, isWin, value }) => {
        setRollerState({
          isAuto,
          isWin,
          value,
        });
      },
    );

    return () => unsubscribe();
  }, []);

  const rocketToValue = Math.max(Number(rollerState.value), 1);

  return (
    <div className={css.root}>
      <h1
        className={clsx(css.payText, {
          [css.lose]: !rollerState.isWin,
        })}>
        <AnimateNumbers
          from={rollerState.isAuto ? rocketToValue : 1}
          to={rocketToValue}
          duration={rocketAnimationTime}
          format={(value) => `${value.toFixed(2)}x`}
        />
      </h1>
    </div>
  );
};

export default React.memo(Roller);
