import { fetchBase } from "utils";
import { LimboCoinLimit } from "../types";

interface FetchReturn {
  success: boolean;
  coins: {
    coin: string;
    maximum_bet: string;
    maximum_profit: string;
    minimum_bet: string;
  }[];
}

export const getLimits = async (
  callBack: (value: { [key: string]: LimboCoinLimit }) => void,
) => {
  const { data } = await fetchBase<FetchReturn>("limbo/get-configuration");

  if (data?.coins) {
    const result = data.coins.reduce((acm, value) => {
      acm[value.coin] = {
        coin: value.coin,
        maximum_bet: Number(value.maximum_bet),
        maximum_profit: Number(value.maximum_profit),
        minimum_bet: Number(value.minimum_bet),
      };

      return acm;
    }, {} as { [key: string]: LimboCoinLimit });

    callBack(result);
  }
};
