import React, { useState } from "react";

import {
  OptionsButton,
  HotKeysModal,
  LiveStats,
  SoundModals,
} from "components";

import LazyComponentLoader from "config/LazyComponentLoader";
import { Chance } from "../Chance";
import { Manual } from "../Manual";
import { Panel } from "../Panel";
import css from "./style.module.scss";

const BettingInformation = React.lazy(() =>
  LazyComponentLoader(import("pages/ModalPages/pages/BettingInformation")),
);

export const MainPanel = () => {
  const [isSoundOpen, setIsSoundOpen] = useState(false);
  const [hotKeyIsOpen, setHotKeyIsOpen] = useState(false);
  const [liveStatsIsOpen, setLiveStatsIsOpen] = useState(false);
  const [bettingInfoIsOpen, setBettingInfoIsOpen] = useState(false);

  const closeHotKey = () => setHotKeyIsOpen(false);
  const closeSound = () => setIsSoundOpen(false);

  const handleLiveStatsClose = () => setLiveStatsIsOpen(false);
  return (
    <>
      <SoundModals isOpen={isSoundOpen} onClose={closeSound} />
      <HotKeysModal isOpen={hotKeyIsOpen} onClose={closeHotKey} />
      <BettingInformation
        isOpen={bettingInfoIsOpen}
        onClose={() => setBettingInfoIsOpen(false)}
        game="limbo"
      />
      <LiveStats
        {...{
          isOpen: liveStatsIsOpen,
          handleClose: handleLiveStatsClose,
        }}
      />

      <Panel />

      {/* <div className={css.line} /> */}
      <div className={css.options}>
        <OptionsButton
          {...{
            withSound: true,
            soundOnClick: () => setIsSoundOpen(!isSoundOpen),
            liveStatOnclick: () => setLiveStatsIsOpen(!liveStatsIsOpen),
            hotKeysOnClick: () => setHotKeyIsOpen(!hotKeyIsOpen),
            bettingInformationOnClick: () =>
              setBettingInfoIsOpen(!bettingInfoIsOpen),
          }}
        />
      </div>
      {/* <div className={css.bigLine} /> */}

      <div className={css.box}>
        <Manual />
        <Chance />
      </div>
    </>
  );
};
