import React, { FC, Suspense } from "react";

import LazyComponentLoader from "config/LazyComponentLoader";
import ProgressFallback from "components/fallbacks/Progress";
import { useIsAutoOpen } from "pages/Limbo";

const Auto = React.lazy(() => LazyComponentLoader(import("../Auto")));

export const WorkPanel: FC = () => {
  const { isAuto } = useIsAutoOpen();

  return (
    <>
      {isAuto && (
        <Suspense fallback={<ProgressFallback color={"#0e5057"} />}>
          <Auto />
        </Suspense>
      )}
    </>
  );
};
