import React from "react";

import { useGetLocale } from "providers";
import { SwitchBox as SwitchBoxBase } from "components";

import { useIsAutoOpen } from "pages/Limbo/";
import css from "./style.module.scss";

const SwitchBox = () => {
  const intl = useGetLocale();
  const { isAuto, setAuto } = useIsAutoOpen();

  return (
    <div className={css.root}>
      <SwitchBoxBase
        isChecked={isAuto}
        text={{
          off: intl.formatMessage({ id: "crashInterface.bet.switch.manual" }),
          on: intl.formatMessage({ id: "crashInterface.bet.switch.auto" }),
        }}
        switchHandleChange={setAuto}
        classes={{
          switch: {
            track: css.switchTrack,
            toggle: css.switchToggle,
          },
        }}
      />
    </div>
  );
};

export default SwitchBox;
