import React, { useEffect, useState } from "react";
import clsx from "clsx";

import {
  gamePathNames,
  getSoundEffectNames,
  useSoundSystemControl,
} from "components";
import css from "./style.module.scss";

export const rocketAnimationTime = Number(css.rocketAnimationTime),
  fireAnimationTime = Number(css.fireAnimationTime);

const timeouts: NodeJS.Timeout[] = [];

interface Props {
  isStarted: boolean;
  setNoStarted: () => void;
}

export const Rocket = ({ isStarted, setNoStarted }: Props) => {
  const [isBurn, setIsBurn] = useState(false);
  const {
    controlFunctions: { playEffect },
  } = useSoundSystemControl();
  const { fly, start } = getSoundEffectNames(gamePathNames.LIMBO);

  useEffect(() => {
    if (isStarted) {
      playEffect(start);
      const to = setTimeout(() => {
        playEffect(fly);
      }, fireAnimationTime);
      const t = setTimeout(() => {
        setIsBurn(true);

        const t = setTimeout(() => {
          setNoStarted();
          setIsBurn(false);
        }, fireAnimationTime);
        timeouts.push(t);
      }, rocketAnimationTime);
      timeouts.push(to);
      timeouts.push(t);
    }
  }, [isStarted]);

  useEffect(() => {
    return () => {
      timeouts.forEach((t) => clearTimeout(t));
    };
  }, []);

  return (
    <>
      <img
        className={clsx(css.rocket, {
          [css.flying]: isStarted,
        })}
        src="/assets/limbo/img/ship.png"
        alt="ship"
      />
      <div>
        <img
          className={clsx(css.fire, css.leftFire, {
            [css.burning]: isStarted,
          })}
          src="/assets/limbo/img/fire.svg"
          alt="fire"
        />
        <img
          className={clsx(css.fire, css.rightFire, {
            [css.burning]: isStarted,
          })}
          src="/assets/limbo/img/fire.svg"
          alt="fire"
        />
      </div>
      <div className={`${css.explosion} ${isBurn ? css.burn : css.noBurn}`} />
      <div
        className={clsx(css.shadow, {
          [css.flyShadow]: isStarted,
        })}
      />
    </>
  );
};
