import React, { FocusEventHandler, useEffect, useRef, useState } from "react";
import clsx from "clsx";

import { rangeBetween, validateNumbers } from "utils";
import { Button, InputAdornment } from "components";
import { coinsManager, useCoins } from "hooks";
import { useGetLocale } from "providers";
import { Warning } from "components";

import { useLimboControl, fromUsd, toUsd, useIsAutoStarted } from "pages/Limbo";
import { LimboInput } from "../../../Input";
import css from "./style.module.scss";
import { useHotKeyListener } from "components/HotKeys/Listener";

interface ManualInputs {
  bet: HTMLInputElement | null;
  profit: HTMLInputElement | null;
}

export const Manual = () => {
  const intl = useGetLocale();
  const { manager } = useLimboControl();
  const { showInUSD, selectedCoin } = useCoins();
  const { isStarted } = useIsAutoStarted();

  const [warning, setWarning] = useState(false);
  const isReady = useRef(false);
  const inputElements = useRef<ManualInputs>({
    bet: null,
    profit: null,
  });

  const setInputValues = ({
    bet: betValue,
    profit: profitValue,
  }: {
    bet?: string | number;
    profit?: string | number;
  }) => {
    const { bet, profit } = inputElements.current;

    //@ts-ignore -- because HTMLInputElement.value: string // Type 'number' is not assignable to type 'string'
    if (bet && betValue) bet.value = betValue;
    //@ts-ignore -- because HTMLInputElement.value: string // Type 'number' is not assignable to type 'string'
    if (profit && profitValue) profit.value = profitValue;
  };

  const recalculateInUsd = (values: { bet: number; profit: number }) => {
    values.bet = toUsd(values.bet);
    values.profit = toUsd(values.profit);

    return { ...values };
  };

  const getLimits = () =>
    manager.panel.limits[selectedCoin?.coin || "GEM"] || {
      maximum_bet: 1,
      minimum_bet: 0.0000001,
    };

  const setBetAmount = (value: number) =>
    manager.emitter.setManualValues("betAmount", value);
  const handleMinMax = (type: "min" | "max") => {
    const { maximum_bet, minimum_bet } = getLimits();
    const { balance } = coinsManager.selectedCoin || {};
    setBetAmount(
      type === "min"
        ? minimum_bet
        : Math.min(maximum_bet, Number(balance) || maximum_bet),
    );
  };
  const handleMultiply = (type: "half" | "double") => {
    const { maximum_bet, minimum_bet } = getLimits();
    const { balance } = coinsManager.selectedCoin || {};

    const maxBetValue = Math.min(maximum_bet, Number(balance) || maximum_bet);

    const value = manager.panel.manual.betAmount;
    setBetAmount(
      type === "double"
        ? rangeBetween(value * 2, minimum_bet, maxBetValue)
        : rangeBetween(value / 2, minimum_bet, maxBetValue),
    );
  };
  const handleBlur: FocusEventHandler<HTMLInputElement> = ({ target }) => {
    const { maximum_bet, minimum_bet } = getLimits();
    const betValue = fromUsd(target.value);
    const newValue = Number(
      rangeBetween(betValue, minimum_bet, maximum_bet).toFixed(8),
    );

    setBetAmount(newValue);
  };

  useEffect(() => {
    const removeListener = manager.emitter.manualValuesListener(
      (manualValues) => {
        const { betAmount, profit } = manualValues;

        const values = recalculateInUsd({
          bet: betAmount,
          profit,
        });
        setInputValues({
          bet: values.bet.toFixed(8),
          profit: values.profit.toFixed(8),
        });
      },
    );

    return () => removeListener();
  }, [manager]);

  useEffect(() => {
    const { betAmount, profit } = manager.panel.manual;
    const values = recalculateInUsd({
      bet: betAmount,
      profit,
    });

    setInputValues({
      bet: values.bet.toFixed(8),
      profit: values.profit.toFixed(8),
    });
  }, [showInUSD]);

  useEffect(() => {
    if (isReady.current) {
      handleMinMax("min");
      return;
    }

    isReady.current = true;
  }, [selectedCoin?.coin]);

  useHotKeyListener({
    callbackKeyA: () => handleMultiply("half"),
    callbackKeyS: () => handleMultiply("double"),
  });

  const amountButtons = (
    <>
      <Button
        disabled={isStarted}
        className={clsx(css.button, css.buttonLeft)}
        onClick={() => handleMinMax("min")}>
        min
      </Button>
      <Button
        disabled={isStarted}
        className={clsx(css.button)}
        onClick={() => handleMultiply("half")}>
        1/2
      </Button>
      <Button
        disabled={isStarted}
        className={clsx(css.button)}
        onClick={() => handleMultiply("double")}>
        2x
      </Button>
      <Button
        disabled={isStarted}
        className={clsx(css.button, css.buttonRight)}
        onClick={() => setWarning(true)}>
        max
      </Button>
    </>
  );

  return (
    <>
      <div className={css.root}>
        <div className={css.amountWrapper}>
          <LimboInput
            label={intl.formatMessage({ id: "dice.manual.base.bet" })}
            withImg
            ref={(node) => (inputElements.current.bet = node)}
            disabled={isStarted}
            onChange={({ target }) =>
              (target.value = validateNumbers(target.value))
            }
            onBlur={handleBlur}
            endAdornment={
              <InputAdornment position="end" className={css.buttonWrapper}>
                {amountButtons}
              </InputAdornment>
            }
          />

          <div className={css.mobileButtonWrapper}>{amountButtons}</div>

          <Warning
            isOpen={warning}
            onClose={() => setWarning(false)}
            callBack={() => handleMinMax("max")}
          />
        </div>
        <LimboInput
          label={intl.formatMessage({ id: "dice.manual.base.profit" })}
          withImg
          disabled
          readOnly
          ref={(node) => (inputElements.current.profit = node)}
          className={css.profit}
        />
      </div>
    </>
  );
};
