import React, { FunctionComponent, useEffect, useMemo } from "react";
import { useSoundSystemControl } from "components";
import { useSeed } from "providers";

import { LimboManager } from "./manager";
import Context from "./Context";

export const LimboControlProvider: FunctionComponent = ({ children }) => {
  const seed = useSeed();
  const audio = useSoundSystemControl();

  const manager = useMemo(() => new LimboManager(seed, audio), []);
  manager.seed = seed;

  useEffect(() => {
    return () => {
      manager?.emitter.setAuto({ isStarted: false });
      manager?.emitter.removeAllListeners();
    };
  }, []);

  if (!manager) {
    return null;
  }

  return (
    <Context.Provider
      value={{
        manager: manager,
      }}>
      {children}
    </Context.Provider>
  );
};
