import { useEffect, useState } from "react";
import { useLimboControl } from "../index";

export const useIsAutoStarted = () => {
  const { manager } = useLimboControl();
  const [isStarted, setIsStarted] = useState(false);

  const setStarted = (value: boolean) =>
    manager.emitter.setAuto({
      isOpen: value,
    });

  useEffect(() => {
    const removeListener = manager.emitter.autoListener(({ isStarted }) =>
      setIsStarted(isStarted),
    );
    return () => {
      removeListener();
    };
  }, []);

  return {
    isStarted,
    setStarted,
  };
};
