import React, { FC, useRef, useEffect, FocusEventHandler } from "react";

import { rangeBetween, validateNumbers } from "utils";
import { InputAdornment } from "components";
import { useGetLocale } from "providers";

import { useLimboControl, limboConfig, changeRange } from "pages/Limbo";
import { LimboInput } from "../../../Input";
import css from "./style.module.scss";

interface InputElements {
  payout: HTMLInputElement | null;
  chance: HTMLInputElement | null;
}

export const Chance: FC = () => {
  const intl = useGetLocale();
  const { manager } = useLimboControl();

  const inputElements = useRef<InputElements>({
    chance: null,
    payout: null,
  });

  const handleChange = (key: keyof InputElements, value: string | number) => {
    if (inputElements.current[key] && value)
      //@ts-ignore -- because HTMLInputElement.value: string // Type 'number' is not assignable to type 'string'
      inputElements.current[key].value = value;
  };

  const setChance = (value: string | number) => handleChange("chance", value);
  const setPayout = (value: string | number) => handleChange("payout", value);

  const payoutBlur: FocusEventHandler<HTMLInputElement> = ({ target }) => {
    const { max, min } = limboConfig.payout;
    const payoutValue = Number(
      rangeBetween(Number(target.value), min, max).toFixed(2),
    );
    manager.emitter.setManualValues("payout", payoutValue);
  };

  const chanceBlur: FocusEventHandler<HTMLInputElement> = ({ target }) => {
    const { max, min } = limboConfig.chance;
    const chance = Number(
      rangeBetween(Number(target.value), min, max).toFixed(changeRange),
    );

    manager.emitter.setManualValues("chance", chance);
  };

  useEffect(() => {
    const unSubscribe = manager.emitter.manualValuesListener(
      ({ chance, payout }) => {
        setChance(chance);
        setPayout(payout);
      },
    );

    return () => {
      unSubscribe();
    };
  }, []);

  return (
    <>
      <div className={css.root}>
        <LimboInput
          ref={(node) => (inputElements.current.payout = node)}
          label={intl.formatMessage({ id: "limboInterface.controller.payout" })}
          onChange={({ target }) =>
            (target.value = validateNumbers(target.value))
          }
          onBlur={payoutBlur}
        />
        <LimboInput
          ref={(node) => (inputElements.current.chance = node)}
          endAdornment={<InputAdornment position="end">%</InputAdornment>}
          label={intl.formatMessage({ id: "limboInterface.controller.chance" })}
          onChange={({ target }) =>
            (target.value = validateNumbers(target.value))
          }
          onBlur={chanceBlur}
        />
      </div>
    </>
  );
};
