import React, { useRef, FunctionComponent, MouseEventHandler } from "react";
import { FacebookShareButton, TwitterShareButton } from "react-share";
import { useGetLocale } from "providers";

import { Button, Input, Icon } from "components";
import facebookIcon from "assets/icons/facebook-white.svg";
import twitterIcon from "assets/icons/twitter-white.svg";

import css from "./style.module.scss";

interface Props {
  handleCopyToBuffer: (
    props: any,
    ref: React.RefObject<HTMLInputElement> | null,
  ) => void;
  onClose: () => void;
  shareLink: string;
  shareTitle: string;
}

export const Share: FunctionComponent<Props> = ({
  handleCopyToBuffer,
  onClose,
  shareLink,
  shareTitle,
}) => {
  const intl = useGetLocale();
  const inputRef = useRef<HTMLInputElement>(null);

  const linkInputCopyToBuffer: MouseEventHandler<HTMLDivElement> = (event) => {
    handleCopyToBuffer(event, inputRef);
  };
  const linkButtonCopyToBuffer = () => {
    if (shareLink) {
      handleCopyToBuffer(shareLink, inputRef);
    }
  };

  return (
    <div className={css.root}>
      <span>
        {intl.formatMessage({
          id: "gameTemplate.statistics.yourBetsTable.share.linkToShare",
        })}
      </span>
      <Input
        readOnly
        ref={inputRef}
        onClick={linkInputCopyToBuffer}
        value={shareLink}
        className={css.input}
        endAdornment={
          <Button icon className={css.icon} onClick={linkButtonCopyToBuffer}>
            <Icon variant="copy" withoutButton />
          </Button>
        }
      />
      <div className={css.line} />
      <span>
        {intl.formatMessage({
          id: "gameTemplate.statistics.yourBetsTable.share.shareToSocial",
        })}
      </span>
      <div className={css.socialContainer}>
        <div className={css.social}>
          <FacebookShareButton url={shareLink} quote={shareTitle}>
            <img className={css.iconBox} src={facebookIcon} />
          </FacebookShareButton>
          <span>Facebook</span>
        </div>
        <div className={css.social}>
          <TwitterShareButton title={shareTitle} url={shareLink}>
            <img className={css.iconBox} src={twitterIcon} />
          </TwitterShareButton>
          <span>Twitter</span>
        </div>
      </div>
      <div className={css.line} />
      <div className={css.buttonWrapper}>
        <Button onClick={onClose} className={css.button}>
          {intl.formatMessage({
            id: "gameTemplate.statistics.yourBetsTable.share.cancel",
          })}
        </Button>
      </div>
    </div>
  );
};

export default Share;
