import React from "react";

import { Button, notifyBarManager } from "components";
import { useGetLocale } from "providers";
import { useLocalStorage } from "hooks";

import { useLimboControl, useIsAutoStarted, useIsAutoOpen } from "pages/Limbo";
import { useHotKeyListener } from "components/HotKeys/Listener";
import SwitchBox from "./SwitchBox";
import css from "./style.module.scss";

export const Panel = () => {
  const intl = useGetLocale();
  const [token] = useLocalStorage("token");
  const { isStarted } = useIsAutoStarted();
  const { isAuto: isOpen } = useIsAutoOpen();

  const { manager } = useLimboControl();

  const play = () => {
    if (token) {
      manager.onClick();
    } else {
      notifyBarManager.error({
        message: intl.formatMessage({ id: "crashInterface.messages.noLogin" }),
        timeOut: 2000,
      });
    }
  };

  useHotKeyListener({
    callbackSpace: play,
  });

  const getButtonText = () => {
    const format = (id: string) => intl.formatMessage({ id });

    if (!isOpen) {
      return format("plinko.bet.label");
    } else {
      if (isStarted) {
        return format("plinko.autoBet.stopLabel");
      } else {
        return format("plinko.autoBet.startLabel");
      }
    }
  };

  return (
    <div className={css.root}>
      <SwitchBox />
      <Button className={css.button} onClick={play}>
        {getButtonText()}
      </Button>
    </div>
  );
};
