import { rangeBetween } from "utils";
import { changeRange, limboConfig } from "../../config";

const maxPayout = 9700;

export const calcChance = (payout: number) => {
  const { max, min } = limboConfig.chance;
  let chance = parseFloat((maxPayout / (payout * 100)).toFixed(changeRange));

  return rangeBetween(chance, min, max);
};

export const calcPayout = (chance: number) => {
  const winning_chance = Number(chance.toFixed(changeRange));
  let result = maxPayout / (winning_chance * 100);
  result = rangeBetween(result, limboConfig.payout.min, limboConfig.payout.max);

  return Number(result.toFixed(2));
};
