import { coinsManager } from "hooks";

export const fromUsd = (value: string, isIgnore = true) => {
  const { showInUSD, selectedCoin } = coinsManager;
  if (showInUSD && selectedCoin?.coin !== "GEM" && isIgnore === true) {
    return Number(
      (Number(value) / Number(selectedCoin?.price || 1)).toFixed(8),
    );
  }

  return Number(Number(value).toFixed(isIgnore === true ? 8 : 0));
};

export const toUsd = (value: number) => {
  const { showInUSD, selectedCoin } = coinsManager;

  if (showInUSD && selectedCoin?.coin !== "GEM") {
    return value * Number(selectedCoin?.price || 0);
  }

  return value;
};
