import { track } from "utils";

const limboBetValueLocalName = "limboBetAmount",
  limboPayoutLocalName = "limboPayout";

export const getFromLocal = () => {
  try {
    const betAmount = Number(localStorage.getItem(limboBetValueLocalName) || 0);
    const payout = Number(localStorage.getItem(limboPayoutLocalName) || 0);

    return {
      betAmount,
      payout,
    };
  } catch (error) {
    track()?.errors(error);

    return {
      betAmount: 0,
      payout: 0,
    };
  }
};

export const saveToLocal = ({
  betAmount,
  payout,
}: {
  betAmount?: number | string;
  payout?: number | string;
}) => {
  if (betAmount) localStorage.setItem(limboBetValueLocalName, "" + betAmount);
  if (payout) localStorage.setItem(limboPayoutLocalName, "" + payout);
};
